import {Component} from "react";
import Content from "../../../layout/Content";
import Crud from "../../../components/Crud";
import apis from '../../../config/setting'
import {Button, Form, Input, message, Select, Spin} from "antd";
import {del, get} from "../../../utils/requestUtil";

export default class Attribute extends Component {

    state = {
        spinning: false,
        categoryNameList: [],
        refresh: false,
    }

    UNSAFE_componentWillMount = () => {
        this.initData();
    };

    initData = () => {
        this.setState({spinning: true});
        get(apis.categoryApi).then(json => {
            let data = json.data;
            this.setState({spinning: false, categoryNameList: data});
        }).catch(error => {
            console.error('', error);
            message.error("");
        });
    }

    generateFormContext = () => {
        const {categoryNameList} = this.state;
        const {Option} = Select;
        let childOption = categoryNameList.map(item => <Option key={item.id} value={item.id}>{item.name}</Option>);
        return (
            <div>
                <Form.Item
                    label=""
                    name="categoryId"
                    rules={[
                        {
                            required: true,
                            message: "Please select categoryName!",
                        },
                    ]}
                >
                    <Select>{childOption}</Select>
                </Form.Item>
                <Form.Item label="" name="name" rules={[
                    {
                        required: true,
                        message: "Please input name!",
                    },
                ]}>
                    <Input/>
                </Form.Item>
                <Form.Item label="" name="value" rules={[
                    {
                        required: true,
                        message: "Please input value!",
                    },
                ]}>
                    <Input/>
                </Form.Item>
            </div>
        )
    }

    addTopComponent = () => {
        const buttonClick = () => {
            this.setState({spinning: true});
            del(apis.attributeApi + '/clean').then(json => {
                const {refresh} = this.state;
                message.success("");
                this.setState({spinning: false, refresh: !refresh});
            }).catch(error => {
                console.error('', error);
                message.error("");
            });
        }
        return (
            <Button type="primary" danger onClick={buttonClick}></Button>
        );
    }

    content = () => {
        const {spinning, refresh} = this.state;
        const columns = [
            {
                title: "id",
                dataIndex: "id",
                key: "id",
            },
            {
                title: "",
                dataIndex: "categoryName",
                key: "categoryName",
            },
            {
                title: "",
                dataIndex: "name",
                key: "name",
            },
            {
                title: "",
                dataIndex: "value",
                key: "value",
            },
        ];
        return (
            <div>
                <Spin spinning={spinning}>
                    <Crud url={apis.attributeApi}
                          tabColumns={columns}
                          generateFormContext={this.generateFormContext.bind(this)}
                          addTopComponent={this.addTopComponent.bind(this)}
                          refresh={refresh}
                          failJump
                    />
                </Spin>
            </div>
        );
    };

    render() {
        return <Content view={this.content()}/>;
    }
}
